.wrapper {
  margin: 0 0 0 200px;
  padding: 20px ;
  min-width: 800px;
  box-sizing: border-box;
  overflow: hidden;
  background-color: #f8f9fa;
}
.wrapper .header {
  height: 130px;
  max-width: 1070px;
  background-color: #fff;
}
.wrapper .header .title {
  padding: 30px 0 0 50px;
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 25px;
  color: #000;
}
.wrapper .header .up_time {
  font-size: 14px;
  padding-left: 50px ;
}
.wrapper .avatar {
  width: 78px;
  height: 80px;
}
.wrapper .avatar img {
  width: 46px;
  height: 46px;
  border-radius: 23px;
}
.wrapper .content {
  background-color: #fff;
  margin: 10px 0;
  padding: 30px 60px;
  max-width: 1070px;
}
.wrapper .antd_pagination {
  padding-top: 31px;
  text-align: center;
}
@media (max-width: 1070px) {
  .wrapper {
    margin: 0;
  }
}
